
.contacts {
  padding: 0 20px 0 20px;
}

.contacts-container {
  width: 320px;
  padding: 0 20px;
  // width: 100%;
  margin: 0 auto;

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 16px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
  }

  @media screen and (min-width: 320px) {
    padding-top: 60px;
  }

  @media screen and (min-width: 768px) {
    padding-top: 80px;
  }

  @media screen and (min-width: 1200px) {
    padding-top: 120px;
  }
}
.contacts {
  background-image: linear-gradient(
    to bottom,
    var(--dominant-color) 0%,
    var(--dominant-color) 22%,
    var(--background-color-pink) 22%,
    var(--background-color-pink) 100%
  );
}

.contacts-wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 32px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    gap: 32px;
    margin-bottom: 60px;
  }
}

.contacts-card {
  padding: 40px 32px;
  background-color: var(--dominant-color);
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
}

.cafe {
  display: inline-block;
  background-color: var(--item-coffee-color);
  width: 80px;
  height: 16px;
  font-size: 12px;
  line-height: 1.5;
  text-align: center;
  color: var(--dominant-color);
  border-radius: 4px;
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
    width: 73px;
    height: 28px;
    font-size: 16px;
    line-height: calc(30px / 16px);
    margin-bottom: 32px;
  }
}
.truck {
  display: inline-block;
  background-color: var(--item-green-color);
  width: 130px;
  height: 16px;
  font-size: 12px;
  line-height: 1.5;
  text-align: center;
  color: var(--dominant-color);
  border-radius: 4px;
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
    width: 138px;
    height: 28px;
    font-size: 16px;
    line-height: calc(30px / 16px);
    margin-bottom: 32px;
  }
}

.contacts-card__city {
  font-size: 12px;
  line-height: calc(22px / 12px);
  letter-spacing: 0.04em;
  color: var(--secondary-color);
  margin-bottom: 8px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26px / 14px);
    margin-bottom: 12px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(30px / 16px);
    margin-bottom: 8px;
  }
}

.contacts-card__item {
  font-size: 12px;
  line-height: calc(18px / 12px);
  color: var(--grey-color);
  margin-bottom: 24px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(23px / 14px);
    margin-bottom: 32px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(29px / 16px);
  }
}

.contacts-card__schedule {
  padding: 24px 0;
  border-top: 1px solid var(--border-color);
  border-bottom: 1px solid var(--border-color);
  margin-bottom: 24px;

  @media screen and (min-width: 768px) {
    padding: 32px 0;
    margin-bottom: 32px;
  }
}

.contacts-card__day {
  font-size: 12px;
  line-height: calc(22px / 12px);
  color: var(--secondary-color);

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26px / 14px);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(30px / 14px);
  }
}

.contacts-card__time {
  font-size: 12px;
  line-height: calc(22px / 12px);
  color: var(--accent-secondary-color);

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26px / 14px);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(30px / 14px);
  }
}

.time-margin {
  margin-bottom: 8px;

  @media screen and (min-width: 768px) {
    margin-bottom: 12px;
  }
}

.auth__link {
  font-size: 12px;
  line-height: calc(18px / 12px);
  color: var(--grey-color);

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26px / 14px);
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(29px / 16px);
  }

  &:hover,
  &:focus {
    color: var(--accent-color);
  }
}

.contacts-btn {
  display: flex;
  flex-direction: column;
  gap: 10px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    gap: 15px;
    justify-content: center;
  }

  @media screen and (min-width: 768px) {
    gap: 12px;
  }
}
